.home-page {
  @extend .app-page;
  scroll-snap-type: y proximity;
  height: calc(100vh - #{$header-height});
  overflow-y: scroll;

  .home-page__section {
    scroll-snap-align: start;
    height: 100%;
    display: flex;
    flex-direction: column;
    opacity: 0.7;
    z-index: 1;

    &.left {
      align-items: flex-start;
      text-align: left;
    }

    &.right {
      align-items: flex-end;
      text-align: right;
    }

    &.bottom {
      height: 30%;
    }
  }

  .home-page__section-title {
    padding: 16px;
    font-size: 32px;
    text-transform: uppercase;
    opacity: 1;
    z-index: 2;
  }

  .home-page__section-text {
    font-size: 14px;
    padding: 16px;
    max-width: 60%;
    opacity: 1;
    z-index: 2;
  }

  .home-page__link-container {
    margin: 16px;
    height: 64px;
  }

  img {
    height: 100%;
  }
}